<template>
  <Row>


    <Col :xs="{span:20,offset:1} " :sm="{span:8,offset:8}" :md="{span:8,offset:8}" :lg="{span:8,offset:8}" style="width: 342px" >
      <Card>
        <strong slot="title" style="font-size: 20px;font-family: 微软雅黑">轻松订</strong>
      <router-view></router-view>

        <Button to="/request" style="margin-top: 50px;" v-show="islog"><b>新用户注册</b></Button>
        <Button to="/login" style="margin-top: 50px;margin-left: 20px" v-on:click="islog=!islog" v-show="islog"><b>登录</b></Button>

        <Button to="/request" style="margin-top: 50px;" v-show="isrequest"><b>前往登录</b></Button>
        <Button to="/login" style="margin-top: 50px;margin-left: 20px" v-on:click="islog=!isrequest" v-show="isrequest"><b>注册</b></Button>

      </Card>
    </Col>


  </Row>
</template>

<script>
    export default {
        name: 'userlog',
      data () {
        return {
         islog:true,
          isrequest:false
        }
      }
    }
</script>

<style scoped>

</style>



